
@font-face {
    font-family: "iconfont";
    src: url('../libs/font/iconfont.eot');
    /* IE9*/
    src: url('../libs/font/iconfont.eot#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('../libs/font/iconfont.woff') format('woff'), /* chrome, firefox */
    url('../libs/font/iconfont.ttf') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
    url('../libs/font/iconfont.svg#iconfont') format('svg');
    /* iOS 4.1- */
}
.iconfont {
    font-family: "iconfont" !important;
    font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
}
input {
    -webkit-user-select: auto;
    /*webkit浏览器   解决iOS  bug*/
}
