    padding: 0;
    margin: 0;
    -webkit-overflow-scrolling: touch;
    background-color: #F6F6F6;
  }*/
#app {
    height: 100%;
}
  /*文字潮款省略*/
.text_ellipsis {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
  /*文字显示2行*/
.text_ellipsis_2 {
    -webkit-box-orient: vertical;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    word-break: break-all;
}
  /*文字显示5行*/
.text_ellipsis_5 {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    word-break: break-all;
}
.fade-enter-active,
  .fade-leave {
    -webkit-transition: opacity 0.7s;
    transition: opacity 0.7s
}
.fade-enter,
  .fade-leave-active {
    opacity: 0
}
.mint-indicator {
    -webkit-transition: opacity .2s linear;
    transition: opacity .2s linear;
}
.mint-indicator-wrapper {
    top: 50%;
    left: 50%;
    position: fixed;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.7);
    color: white;
    box-sizing: border-box;
    text-align: center;
}
  /*
flex: 定义布局为盒模型
flex-v: 盒模型垂直布局
flex-1: 子元素占据剩余的空间
flex-align-center: 子元素垂直居中
flex-pack-center: 子元素水平居中
flex-auto 
flex-none
flex-pack-justify: 子元素两端对齐
兼容性：ios4+、android 2.3+、 winphone8+
*/
.flex {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}
.flex-v {
    -webkit-box-orient: vertical;
    -webkit-flex-direction: column;
    -ms-felx-direction: column;
    -ms-flex-direction: column;
        flex-direction: column;
}
.flex-1 {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
}
.flex-none {
    -webkit-box-flex: none;
    -webkit-flex: none;
    -ms-flex: none;
    flex: none;
}
.flex-auto {
    -webkit-box-flex: auto;
    -webkit-flex: auto;
    -ms-flex: auto;
    flex: auto;
}
  /*align-items定义项目在交叉轴上如何对齐*/
.flex-align-center {
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
}
  /*justify-content定义了项目在主轴上的对齐方式（居中）*/
.flex-pack-center {
    -webkit-box-pack: center;
    -wekkit-justify-content: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
            justify-content: center;
}
  /*适配 ios 8 不支持flex;*/
.flex_ {
    display: inline-block;
}



